// Anchors
//-----------------------------------------------------------------------------
 a {
   @extend .primary-color_color; 
 }

a:focus {
  @extend .primary-color_color; 
}

 a.active,
 a:active,
 a:hover {
   @extend .primary-color_color; 
 }

