blockquote {
  margin: 40px 0 40px 20px;
  p {
    .wd-post__content &, & {
      font-size: 1.3rem;
    }
  }
}

mark {
  background-color: rgba(255,255,0,0.2);
  color: #000;
}



table thead th,
table thead td,
table tfoot th,
table tfoot td {
  padding: .5rem .625rem .25rem;
}
